import Vue from 'vue'
import VueRouter from 'vue-router'

// 路由懒加载导入（没进入的页面不加载，加快页面加载速度）
const Login = () => import(/* webpackChunkName: login_home_welcome" */ '../pages/login/Login.vue')
const Home = () => import(/* webpackChunkName: login_home_welcome" */ '../pages/home/Home.vue')
const Test = () => import(/* webpackChunkName: login_home_welcome" */ '../pages/test/Test.vue')

// 全局注册路由
Vue.use(VueRouter)

// 配置路由
const router = new VueRouter({
    routes: [
        // 进入网站，即定向到home主页
        { path: '/', redirect: '/home' },
        { path: '/home', component: Home },
        { path: '/login', component: Login },
        { path: '/test', component: Test },
    ]
})

// 全局前置守卫：初始化时执行、每次路由切换前执行
// to 将要访问的路径
// from 代表从哪个路径跳转而来
// next 是函数，标识放行
router.beforeEach((to, from, next) => {
    // if (to.path === '/login') return next()
    // 获取token
    // const tokenStr = window.sessionStorage.getItem('token')
    // 若没有token，代表没登录，强制进入登录页面
    // if (!tokenStr) return next('/login')
    // 否则跳转进登录后要进入的页面
    next()
})

// 向外共享路由
export default router